export const requiredInput = (value) => {
    if (value) return undefined
    return ' '
}

export const maxLenghtCreator = (maxLength) => (value) => {
    if (value && value.length > maxLength) return ` ${maxLength}  `
    return undefined
}

export const password = (value) => {
    if(value) {
        if (value.length < 4) return ' 4 '
        else if (value.length > 32) return ' 32 '
    }
    return undefined
}

export const ivReques = (value) => {
    if(value) return undefined
    return '     '
}


